import React from 'react';
import { Component } from 'react';
import  Input from './Input';
import List from './List';

export default class TodoList extends Component{
    constructor(){
        super();
        this.state={
            todos:[]
        }
    }
    handleSubmitTodos=(todo)=>{
        this.state.todos.push(todo);
        this.setState({
            todos:this.state.todos
        })
    };
    componentDidUpdate(){//每次更新时才执行
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
        console.log(1);
    }
    delTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index!=idx)
        })
    }
    toggle=(index)=>{
        let newTodos=JSON.parse(JSON.stringify(this.state.todos))
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                 return<li>
                <input
                onClick={()=>this.toggle(idx)}  
                checked={done} 
                type="checkbox"/>
                <span>{todo.title}</span>
                <button onClick={()=>this.delTodo(idx)}>delete</button>
                </li>
            }
        })
    }
    render(){
        const {todos,inpValue}=this .state
        var arr1=todos.filter(item=>!item.done)
        var arr2=todos.filter(item=>item.done)
        return(
            <div>
                <Input onSubmit={this.handleSubmitTodos}/>
                <List todos={this.state.todos}/>
                <div>
                    <h2>正在进行({arr1.length})</h2>
                    <ul>
                         {this,this.renderTodos(false)}
                    </ul>
                    <h2>已经完成 ({arr2.length})</h2>
                    <ul>
                     {this,this.renderTodos(true)}
                    </ul>
                </div>
            </div>
        )
    }
}

