import React from 'react';
import { Component } from 'react';
//import Input from './Input';

export default class List extends Component {
    static defaultProps={
        todos:[]
    }
    render(){
        return(
            <div><h2>列表</h2>
                <ul>
                    {this.props.todos.map((todo,index)=>{
                   return(
                        <li>
                        <span>{todo.title}</span>
                        </li>)
                   })}
                </ul>
            </div>

        )
    }
}

