import { render } from '@testing-library/react';
import React from 'react';
import { Component } from 'react';

export default class Input extends Component {
    constructor(){
        super();
        this.state={
            innpValue:'',
        }   
    }
    handleChange=(e)=>{
        console.log(e.target.value);
        this.setState({innpValue:e.target.value})
    }
    addTodo=()=>{
        if(this.props.onSubmit){
            this.props.onSubmit({
            //     todos:[{
            //         title:this.state.innpValue,
            //         done:false
            //     },
            // ...this.state.todos]
            title:this.state.innpValue,
            done:false           
            })
        }
        //增加用户体验，提交后清空原来的值
        this.setState({
            innpValue:'',
            //nodes:[]
        })
    }

    render(){
        return(
        <div>
            <input  value={this.state.innpValue} //只要加value就是受控组件
                type='text'
                onChange={this.handleChange}
                ></input>
             <button onClick={this.addTodo}>push</button>
        </div>
    )
    }
}
